/*eslint-disable*/module.exports={messages:{"- **Anspruch**\n- Vermieter:innen haben die Pflicht, die Mietsache in einem Zustand\nzu übergeben und zu erhalten, die der vertraglichen Vereinbarung entspricht. \n- Dafür zahlen die Mietenden die Miete. \n- Wenn während der Mietzeit ein Mangel auftritt, dann muss die Vermieter:in ihn beseitigen. \n- **Ausnahme**: bei Kleinreparaturen bis zu einem Betrag von ca. 100 Euro pro Reparatur\n- wenn das im Vertrag so steht und\n- ein Jahres-Höchstbetrag festgelegt wurde. \n- **Durchsetzung:**\n- Den Anspruch auf Beseitigung des Mangels kann man gegenüber dem\nVermieter geltend machen, indem man ihn auffordert, den Mangel zu beseitigen. Oft geschieht dies gemeinsam\nmit der Mitteilung über den Mangel.":"- **претензия**\n - Арендодатели обязаны держать арендованное имущество в штате\nпередать и получить, что соответствует договорному соглашению. \n - За это арендаторы платят арендную плату. \n - Если в период аренды возникает дефект, арендодатель должен исправить его. \n- **Исключение**: при мелком ремонте до 100 евро за ремонт\n - если так написано в контракте и\n - установлена максимальная годовая сумма. \n- **Правоприменение: **\n - претензия на устранение дефекта может быть предъявлена\nПретендуйте арендодателей, попросив их исправить дефект. Это часто происходит вместе\nс уведомлением о дефекте.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annulierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1500km)**, **400€ (bis zu 3500km)** oder **600€ (über 3500km)**.":"- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annulierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1500km)**, **400€ (bis zu 3500km)** oder **600€ (über 3500km)**.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.":"- Претензия**исправительные дефекты** - это право попросить арендодателя устранить дефект**.\n- Арендодатели обязаны сдать арендованную квартиру и поддерживать ее в пригодном для использования состоянии.\n- Если в период аренды возникает дефект, арендодатель должен устранить его.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mieter können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag mit einem Jahreshöchstbetrag festgelegt wurde.":"- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mieter können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag mit einem Jahreshöchstbetrag festgelegt wurde.","1. Ansprüche Prüfen":"1. Проверить претензии","2. Mögliche Ansprüche":"2. Возможные претензии","3. Nächste Schritte":"3. Дальнейшие шаги","Aufwendungsersatz":"возмещение расходов","Aus Ihren Angaben könnten sich folgende Ansprüche ergeben":"Следующие претензии могут возникнуть в связи с вашей информацией","Ausgleichszahlung":"Ausgleichszahlung","Außergerichtliche Einigung":"Außergerichtliche Einigung","Beratungsangebote":"Beratungsangebote","Beseitigung des Mangels durch die Vermieter:in":"Устранение дефекта арендодателями","Da für das gesamte Projekt nur zwölf Wochen zur Verfügung standen, konnte dieser Pfad leider noch nicht implementiert werden.":"Поскольку для всего проекта было доступно всего двенадцать недель, этот путь пока не удалось реализовать.","Die Justiz im Netz.":"Судебная власть в сети.","Diese Option ist leider noch nicht verfügbar.":"К сожалению, эта опция пока недоступна.","Dieser Pfad wurde noch nicht implementiert":"Этот путь еще не реализован","Dokumentation des Mangels":"Документирование дефекта","Ein Anspruch ist das Recht, ein Verhalten von einer anderen Person zu fordern. Klicken Sie auf die Ansprüche, um mehr darüber zu erfahren.":"Претензия — это право требовать поведения от другого человека. Нажмите на претензии, чтобы узнать больше о них.","Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Klicken Sie auf die Ansprüche, um mehr darüber zu erfahren.":"Претензия — это право требовать что-то от другого лица или компании. Нажмите на претензии, чтобы узнать больше о них.","Einfach, online, jederzeit.":"Легко, онлайн, в любое время.","Erstberatung":"Erstberatung","Fluglinie kontaktieren":"Fluglinie kontaktieren","Gerichtsverfahren":"Gerichtsverfahren","Geschafft! Aus Ihren Angaben könnten sich folgende Ansprüche ergeben":"Готово! Следующие претензии могут возникнуть в связи с вашей информацией","Geschafft! Basierend auf den von Ihnen angegebenen Informationen, könnten die folgenden rechtlichen Ansprüche\nfür Sie in Frage kommen. Ein Anspruch ist das Recht ein bestimmtes Verhalten von einer anderen Person\neinzufordern.":"Готово! Основываясь на предоставленной вами информации, следующие судебные иски могут\nзадаются вопросом для вас. Претензия — это право на определенное поведение другого человека\nтребовать.","Handlungsoptionen vergleichen":"Сравните варианты действий","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"В какой сфере вашей жизни у вас есть проблемы?","Info":"Информация","Ja":"да","Jetzt starten!":"Начать сейчас","Lösungsfinder":"Поиск решений","Mietminderung":"снижение арендной платы","Mängelanzeige bei Ihrer Vermieter:in":"Уведомление о дефектах вашему арендодате:в","Mögliche Ansprüche":"Mögliche Ansprüche","Nein":"нет","Nur kostenlose Angebote anzeigen":"Nur kostenlose Angebote anzeigen","Nächste Schritte":"Nächste Schritte","Online Klage einreichen":"Послать иск в Интернете","Problem einordnen":"Problem einordnen","Rechtlich beraten lassen":"Получите юридическую консультацию","Rechtsproblem einordnen":"Классифицировать юридические проблемы","Schadensersatz":"убытков","Schlichtungsantrag stellen":"Schlichtungsantrag stellen","Unser Angebot":"Наше предложение","Vermieter:in erneut kontaktieren":"Vermieter:in erneut kontaktieren","Weiter":"Далее","Wenn Sie sich über Ihre Ansprüche informiert haben, zeigen wir Ihnen, \nwie Sie diese durchsetzen können. Klicken Sie dafür einfach auf Weiter.":"Как только вы будете проинформированы о своих требованиях, мы покажем вам \nкак вы можете обеспечить их соблюдение. Для этого просто нажмите «Далее».","Wie es funktioniert":"Как это работает","Wobei benötigen Sie Unterstützung?":"Wobei benötigen Sie Unterstützung?","Wobei können wir Ihnen helfen?":"Чем мы можем вам помочь?","Zu den Beratungsangeboten":"Перейти к консультационным услугам","Zum Prototypen":"Прототип","Zurück":"назад","Zweites Schreiben an die Vermieter:in senden":"Второе письмо арендодателю отправить","back":"Zurück","footer.disclaimer":"Эта страница является прототипом без каких-либо гарантий юридической определенности!","footer.info":"Более подробную информацию об этой странице вы можете найти здесь","footer.text":"Создано ⚖️ и 🐌 с ❤️","forward":"Weiter","home.hero_offer":"JustizPortal.de — это предложение Федерального министерства юстиции и защиты прав потребителей в сотрудничестве с федеральными министерствами юстиции федеральных земель. Портал предоставляет обзор ваших прав в различных повседневных ситуациях. Это также помогает классифицировать и решать юридические вопросы и проблемы. Вы будете проинформированы о возможных вариантах действий и получите поддержку в отстаивании ваших прав.","home.howitwork_claims":"Затем мы поможем вам оценить, какие претензии могут существовать в вашем случае. Претензии — это право требовать от других конкретных действий (например, возмещение ущерба), потерпеть (например, сдавать квартиру в субаренду) или воздерживаться от (например, въезда в собственность). invidunt it labore.","home.howitwork_classify":"Во-первых, мы поможем вам выяснить, есть ли юридическая проблема в вашем случае. Мы предоставим вам информацию, адаптированную к вашему случаю.","home.howitwork_heading":"Как это работает","home.howitwork_lawsuit":"Когда все остальные варианты исчерпаны, мы поможем вам подать иск в районный суд онлайн. С этой целью мы объясняем вам процесс нашего онлайн-процесса и предоставляем прозрачную информацию о стоимости судебных разбирательств, а также возможные юридические консультации.","home.howitwork_options":"Исходя из этого, мы предложим возможные решения для <0>вас - первоначально для дружеского разрешения споров с противоположной стороной.</0>","home.howitwork_subheading":"Мы поможем вам решить вашу юридическую проблему в четыре простых шага:","home.howtohelp_footer":"Вы можете узнать больше о юридических проблемах, с которыми мы можем вам помочь, на следующих страницах.","home.sub_heading":"Мы информируем вас о ваших правах в повседневной жизни и помогаем решить юридическую проблему в простых шагах","nextsteps.header":"Ваши следующие возможные шаги к решению проблем","nextsteps.sub_header":"Здесь мы кратко изложили возможные шаги для обеспечения соблюдения ваших требований. Нажмите на флажки один за другим, чтобы узнать больше и получить поддержку.","Über Ansprüche informieren":"Сообщите о претензиях","Über das Justizportal eine Klage einreichen":"Поместите претензию через портал правосудия"}};